raul-toggles {
  display: block;
  font-size: 0.875rem;
}

raul-toggles .r-toggles {
  height: 2.5rem;
  overflow: hidden;
}

raul-toggles .r-toggles__list {
  display: flex;
  overflow-x: auto;
  height: 4rem;
}

raul-toggles .r-toggles__item {
  background-color: #fff;
  border-width: 1px;
  border-color: #C6CCD0;
  font-size: 0.875rem;
  height: 2.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  white-space: nowrap;
  border-right-color: transparent;
}

raul-toggles .r-toggles__item:first-child {
  border-top-left-radius: 0.125rem;
  border-bottom-left-radius: 0.125rem;
}

raul-toggles .r-toggles__item:last-child {
  border-top-right-radius: 0.125rem;
  border-bottom-right-radius: 0.125rem;
  border-right-color: #C6CCD0;
}

raul-toggles .r-toggles__item:hover {
  color: #0076CC;
  text-shadow: 0 0 1px #0076CC;
}

body[modality=keyboard] raul-toggles .r-toggles__item:focus {
  outline: 0;
  border-color: #0076CC;
}

body[modality=keyboard] raul-toggles .r-toggles__item:focus + .r-toggles__item {
  border-left-color: transparent;
}

raul-toggles .r-toggles__item.r-toggles__item--active:not(.r-toggles__item--disabled) {
  background-color: #0076CC;
  border-color: #0076CC;
  color: #fff;
  text-shadow: 0 0 1px #fff;
}

raul-toggles .r-toggles__item.r-toggles__item--disabled {
  background-color: #F7F8F9;
  color: #9BA3A7;
  pointer-events: none;
}

raul-toggles[full-width] .r-toggles__item {
  flex: 1 1 0%;
}